@import "../../common/style/sass/variables";
@import "../../common/style/sass/mixins";

.contacts {
  padding: 70px 0;
  //min-height: 80vh;
  background-color: $bgColor;
  @media screen and (max-width: 600px) {
    padding: 30px 0;
  }

  .container {
    padding: 0 20px;
    margin: 0 auto;
    height: 100%;
    @include flexCenter();
    flex-direction: column;
    justify-content: center;
    flex-wrap: nowrap;
    @media screen and (max-width: 600px) {
      padding: 0;
    }

    .title {
      @include pageTitle();
    }

    .messageForm {
      display: flex;
      flex-direction: column;
      text-align: center;
      width: 100%;
      margin-right: -1px;
      padding: 35px;
      border: 1px solid rgba(255, 255, 255, .07);

      input {
        height: 50px;
        border: none;
        border-bottom: 1px solid rgba(255, 255, 255, .07);
        background: none;
        outline: none;
        @include text(16px, $lightGray);
        margin-bottom: 30px;
        resize: none;
        transition: ease-in-out 0.7s;

        &:focus {
          border-bottom: 1px solid $secondaryColor;
        }
      }

      textarea {
        height: 120px;
        border: none;
        border-bottom: 1px solid rgba(255, 255, 255, .07);
        background: none;
        outline: none;
        @include text(16px, $lightGray);
        margin-bottom: 30px;
        resize: none;
        transition: ease-in-out 0.7s;

        &:focus {
          border-bottom: 1px solid $secondaryColor;
        }
      }

      div {
        align-self: flex-start;

        button {
          @include btn();

        }
      }
    }
  }
}


